html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.App {
  background-color: #f0eff0;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}

.App > * {
  margin: 0 0 10px 0;
}

.App-header {
  display: flex;
  background: -webkit-gradient(linear, left top, left bottom, from(#2d91c2),to(#1e528e));
  background: -webkit-linear-gradient(top, #2d91c2 0%,#1e528e 100%);
  background: linear-gradient(to bottom, #2d91c2 0%,#1e528e 100%);
  font-size: calc(16px + 1vmin);
  color: white;
  padding: 0 10px 0 0;
  align-items: center;
}

.App-logo {
  max-height: 150px;
}

.App-hero {
  background: url(/wiegehtsdireuropa/static/media/hero.8a70a7ff.jpg) no-repeat center center;
  background-size: contain;
  width: 100%;
  padding-top: 30%;
}
.ImageList {
  display: flex;
  overflow-x: auto;
  min-width: 100%;
  min-height: 200px;
}

.ImageList::-webkit-scrollbar {
  display: none;
}

.Image-container {
  min-width: 75%;
  margin: 50px 15px 30px;
	padding: 10px 10px 50px;
  box-shadow: 0 4px 6px rgba(0,0,0,.3);
  font-size: 18px;
  font-weight: bold;
  -webkit-transform: none;
          transform: none;
}

.Image-container::before {
  content: url(/wiegehtsdireuropa/static/media/clothespin.1923b01b.svg);
  display: block;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  height: 117px;
  z-index: 2;
}

.Image-container:after {
  content: attr(title);
}

/* Large screens */
@media all and (min-width: 1200px) {
  .Image-container {
    min-width: 50%;
  }
}
 
/* Rotate all even images -2 degrees */
.Image-container:nth-child(2n) {
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

/* Rotate every fifth image by 2 degrees */
.Image-container:nth-child(3n) {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}

.duotone {
  position: relative;
}

.duotone::after {
  position: absolute;
  content: "\A9   Staatsarchiv Freiburg";
  font-size: 22px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
}

.yellow-red {
  background-color: rgba(221, 204, 0, 1);
}

.yellow-red img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: darken;
}

.yellow-red::after {
  background-color: rgba(221, 0, 0, 1);
  mix-blend-mode: lighten;
  color: white;
}

.green-blue {
  background-color: #00ff36
}

.green-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.green-blue::after {
  background-color: #23278a;
  mix-blend-mode: lighten;
  color: white;
}

.purple-green {
  background-color: #88169d;
}

.purple-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: lighten;
}

.purple-green::after {
  mix-blend-mode: hard-light;
  background: -webkit-gradient(linear, right bottom, left top, from(#75d775), to(#321a5b));
  background: -webkit-linear-gradient(bottom right, #75d775, #321a5b);
  background: linear-gradient(to top left, #75d775, #321a5b);
}

.Image {
  width: 100%;
}
.Boxes {
  background-color: darkseagreen;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}
.BoxAsset {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-radius: 24px;
  font-size: 18px;
  font-weight: 400;
  margin: 10px;
  padding: 7px 10px 5px 10px;
}
